<template>
    <!-- 统计分析 -->
    <div class="num-divI" ref="wrapHeight">
        <div class="row-div" ref="rowHeight">
            <el-row>
                <el-col :span="4">
                    <div>
                        <p>应收欠账（万元）</p>
                        <p class="PI">{{debt}}</p>
                    </div>
                </el-col>
                    <el-col :span="4">
                    <div>
                        <p>实收账数（万元）</p>
                        <p>{{revenue}}</p>
                    </div>
                </el-col>
                <el-col :span="16">
                    <div>
                        <span><span class="dogI"></span>30天内</span>
                        <span><span class="dogII"></span>60-90天</span>
                        <span><span class="dogIII"></span>90-180天</span>
                        <span><span class="dogIV"></span>180-360天</span>
                        <span><span class="dogVI"></span>360-720天</span>
                        <div class="aa" :class="leftClass">
                            <div class="divI" @click="getTypeI(1)"></div>
                            <div class="divII" @click="getTypeI(2)"></div>
                            <div class="divIII" @click="getTypeI(3)"></div>
                            <div class="divIV" @click="getTypeI(4)"></div>
                            <div class="divVI"  @click="getTypeI(5)"></div>
                        </div>
                        <!-- <p>实收账数（亿元）</p>
                        <p>1,670</p> -->
                    </div>
                </el-col>
            </el-row>
        </div>
        <!-- 列表 -->
        <div class="Table_height">
            <ZLCommonTable
                ref="zltable"
                :tableData="receiveData"
                :itemValues="itemValues"
                :tabHeight ="tabHeight"
                @show="show_dialog"
                @delete="delrow"
                @getTabHeight="getTabHeight">
            </ZLCommonTable>
        </div>
    </div>
</template>

<script>
    import ZLCommonTable from '../../../../components/ZLCommonTable'
    export default {
        components:{
            ZLCommonTable,
        },
        data() {
            return {
                receiveData:[],
                itemValues:[{
                    name:'contractname',
                    label:'合同',
                    width:'',
                    type:'normal'
                },{
                    name:'typename',
                    label:'类型',
                    width:'100',
                    type:'normal'
                },{
                    name:'senddate',
                    label:'应收日期',
                    width:'150',
                    type:'normal'
                },{
                    name:'receivableamounts',
                    label:'应收金额',
                    width:'150',
                    type:'normal'
                },{
                    name:'proportion',
                    label:'金额占比',
                    width:'100',
                    type:'normal'
                }],
                tabHeight:200,
                said:1,
                debt:0,
                revenue:0,
                wrapHeight: 342,
                tabHeight: 177,
                leftClass: 'leftNum',
            }
        },
        activated(){
            this.init();
        },
        mounted() {
            this.init();
            this.getDivHeight();
        },
        methods: {
            init() {
                this.getlist();
                // this.initEchart();
                // this.setEchart();
            },
            getDivHeight() {
                const rowHeight = this.$refs.rowHeight.offsetHeight;
                this.wrapHeight = this.$refs.wrapHeight.offsetHeight;
                this.tabHeight = this.wrapHeight - rowHeight - 24;
            },

            //获取数据
            getlist(){
                let tempData = [];
                this.fetch('/mktmfc/forms/getmytarget?datetype='+this.said, {})
                .then((result)=>{
                    if(result.status === 200){
                        tempData =  result.data.myTargetList;
                        this.debt = this.returnMoneyType(result.data.allRerceivable[0].receivableamounts/10000);
                        this.revenue = this.returnMoneyType(result.data.allRerceivable[0].payamounts/10000);
                        for(let i=0; i<tempData.length; i++){
                            if(this.said === 1){
                                tempData[i].typename = '30天内'
                            }else if(this.said === 2){
                                tempData[i].typename = '60-90天'
                            }else if(this.said === 3){
                                tempData[i].typename = '90-180天'
                            }else if(this.said === 4){
                                tempData[i].typename = '180-360天'
                            }else if(this.said === 5){
                                tempData[i].typename = '360-720天'
                            }
                            if(tempData[i].sumamounts === 0){
                                tempData[i].proportion = '100%';
                            }else {
                                if(tempData[i].receivableamounts/tempData[i].sumamounts >=1){
                                    tempData[i].proportion = ((tempData[i].receivableamounts/tempData[i].sumamounts*10000)/100).toFixed(2)+'%'
                                }else {
                                    tempData[i].proportion = (tempData[i].receivableamounts/tempData[i].sumamounts*100).toFixed(2)+'%'
                                }
                            }
                            
                        }
                        this.receiveData = tempData
                    }
                })
            },
            
            //点击天数
            getTypeI(val){
                this.said = val;
                this.getlist();
            },
            //设置表格高度
            getTabHeight(){
                this.$refs.zltable.getHeight(250)
            },
            //echart
            initEchart(){},

            //传参
            setEchart() {},

            show_dialog(){},

            delrow(){},
        }
    }
</script>

<style lang="less" scoped type="text/less">
    .num-divI{
        align-items: center;
        height: 100%;
        .row-div {
            padding-top: 10px;
            div {
                text-align: center;
                p {
                    color: #008AE0;
                    font-size: 25px;
                    font-weight: bold;
                    margin-top: 5px;
                    margin-bottom: 10px;
                }
                p:nth-child(1) {
                    color: #aaa;
                    line-height: 1;
                    font-size: 14px;
                    margin: 3px auto;
                }
                .PI{
                    color: rgb(59, 56, 56);
                }
                span{
                    font-weight: bold;
                    color: #aaa;
                    margin-left: 10px;
                    .dogI {
                        display: inline-block;
                        width: 9px;
                        height: 9px;
                        border-radius: 50%;
                        margin-right: 5px;
                        background-color: #82c979;
                    }
                    .dogII {
                        display: inline-block;
                        width: 9px;
                        height: 9px;
                        border-radius: 50%;
                        margin-right: 5px;
                        background-color: #62d1b7;
                    }
                    .dogIII {
                        display: inline-block;
                        width: 9px;
                        height: 9px;
                        border-radius: 50%;
                        margin-right: 5px;
                        background-color: #629ed1;
                    }
                    .dogIV {
                        display: inline-block;
                        width: 9px;
                        height: 9px;
                        border-radius: 50%;
                        margin-right: 5px;
                        background-color: #db67ff;
                    }
                    .dogVI {
                        display: inline-block;
                        width: 9px;
                        height: 9px;
                        border-radius: 50%;
                        margin-right: 5px;
                        background-color: #ff0b0b;
                    }
                }
            }

            .aa{
                width: 480px;
                height: 30px;
                &.leftNum {
                    margin-left: 19%;
                    @media screen  and (max-width: 1500px) {
                        margin-left: 11%;
                    }
                }
                /*margin-left: 19%;*/
                /*margin-top: 5px;*/
                background-color: #008AE0;
                display: flex;
                .divI {
                    width: 70%;
                    background: #82c979;
                    cursor:pointer;
                }
                .divII {
                    width: 50%;
                    background: #62d1b7;
                    cursor:pointer;
                }
                .divIII {
                    width: 50%;
                    background: #629ed1;
                    cursor:pointer;
                }
                .divIV {
                    width: 20%;
                    background: #db67ff;
                    cursor:pointer;
                }
                .divVI {
                    width: 10%;
                    background: #ff0b0b;
                    cursor:pointer;
                }
            }
        }
        .Table_height{
            /*height: 100px;*/
        }
    }
</style>


